export default {
  url: "reports/lists/chequebooks",
  cols: [{
      th: "",
      td: "account.name",
      type: "text",
      filters: ["account__name__icontains"]
    },
    {
      th: "",
      td: "code",
      type: "number",
      filters: ["serial__icontains"]
    },
    {
      th: " ",
      td: "serial_from",
      type: "number",
      filters: ["serial__icontains"]
    },
    {
      th: " ",
      td: "serial_to",
      type: "number",
      filters: ["serial__icontains"]
    },
    {
      th: "",
      td: "explanation",
      type: "text",
      filters: ["account__icontains"]
    },
  ],
  label: '  '
}



