        <li class="nav-item left">
          <a @click="logout()" class="exit-btn nav-link bg-danger text-white" href="#"></a>
        </li>
      </ul>
    </div>

    <a class="navbar-left" href="#">
      <img class="logo img-fluid" src="/static/img/SobhanAccountingLogo.png" alt>
    </a>
  </nav>
</template>

<script>
export default {
  name: "NavBar",
  data() {
    return {};
  },
  methods: {
    logout() {
      this.setToken("");
      this.$router.push({ name: "Login" });
    }
  },
  computed: {
    username() {
      if (this.user.username.includes("manager")) return "manager";
      return this.user.username;
    }
  }
};
</script>

<style scoped lang="scss">
.logo {
  max-width: 40px;
  padding: -10px;
}

nav {
  margin-bottom: 15px;
}

.dropdown-submenu {
  position: relative;
}

.dropdown-submenu > .dropdown-menu {
  top: 0;
  right: 100%;
  margin-top: -6px;
  margin-left: -1px;
  -webkit-border-radius: 6px 0px 6px 6px;
  -moz-border-radius: 6px 0px 6px 6px;
  border-radius: 6px 0px 6px 6px;
  width: auto;
}

.dropdown-submenu:hover > .dropdown-menu {
  display: block;
  min-width: 200px;
}

.dropdown-submenu > a:after {
  display: block;
  content: " ";
  float: left;
  width: 0;
  height: 0;
  border-color: transparent;
  border-style: solid;
  border-width: 5px 5px 5px 0px;
  border-right-color: #c9c9c9;
  margin-top: 5px;
  margin-left: -10px;
}

.dropdown-submenu:hover > a:after {
  border-right-color: #212121;
}

.exit-btn {
  border-radius: 7px;
}

a.dropdown-item {
  width: 100%;
  display: block;
}
</style>



